<template>
  
    <header class="header">
            <!-- 头部的第一行 -->
            <div class="top">
                <div class="container">
                    <div class="loginList">
                        <p>鱿鱼购欢迎您！</p>
                        <!-- 没有用户名，未登录 -->
                        <p v-if="!userName">
                            <span>请</span>
                            <!-- 声明式导航，务必要有to属性 -->
                            <router-link to="/login">登录</router-link>
                            <router-link to="/register" class="register">免费注册</router-link>
                        </p>
                        <!--  登录了 -->
                        <p v-else>
                            <a>{{userName}}</a>
                            <a class="register" @click="logout">退出登录</a>
                        </p>
                    </div>
                    <div class="typeList">
                        <a href="###">我的订单</a>
                        <a href="###">我的购物车</a>
                        <a href="###">我的鱿鱼购</a>
                        <a href="###">鱿鱼购会员</a>
                        <a href="###">企业采购</a>
                        <a href="###">关注鱿鱼购</a>
                        <a href="###">合作招商</a>
                        <a href="###">商家后台</a>
                    </div>
                </div>
            </div>
            <!--头部第二行 搜索区域-->
            <div class="bottom">
                <h1 class="logoArea">
                    <router-link to="/home" class="logo" title="vueshop"  >
                    <!-- target打开一个新页面 -->
                        <img src="./images/logo.png" alt="">
                        <div class="shopName">鱿鱼购</div>
                    </router-link>
                </h1>
                <div class="searchArea">
                    <form action="###" class="searchForm">
                        <input v-model="keyword" type="text" id="autocomplete" class="input-error input-xxlarge" />
                        <button @click="goSearch" class="sui-btn btn-xlarge btn-danger" type="button">搜索</button>
                    </form>
                </div>
            </div>
        </header>
  
</template>

<script>
export default {
    data() {
        return {
            keyword:''
        }
    },
    methods:{
        goSearch(){
          if(this.$route.query){
              let location={name:'search',params:{keyword:this.keyword||undefined}}
              location.query=this.$route.query
              this.$router.push(location)
          }
            
           },
            //路由传递参数：
            //第一种：字符串形式
            // this.$router.push('/search/'+this.keyword+'?k='+this.keyword.toUpperCase())
            //第二种：模板字符串
            // this.$router.push(`/search/${this.keyword}?k=${this.keyword.toUpperCase()}`)
            //第三种：对象
            // this.$router.push({name:'search',params:{keyword:this.keyword},query:{k:this.keyword.toUpperCase()}})
          async logout(){
               //退出登录
               //需要发请求，通知服务器退出登录
               //清除项目当中的数据【userInfo，token】
               try {
                  await this.$store.dispatch('userLogout')
                   //
                   this.$router.push('/home')
               } catch (error) {
                   
               }
           }
        },
        mounted(){
            //通过全局事件总线清除关键字
            this.$bus.$on("clear",()=>{
                this.keyword=""
            })
        },
        computed:{
            //用户名信息
            userName(){
                return this.$store.state.user.userInfo.name
            }
        }
    }

</script>

<style scope lang="less">
    .header {
        .shopName{
            position:sticky;
            transform: translate(48px,-23px);
            font-weight: 600;
            color: #41b883;
           
        }
        &>.top {
            background-color: #eaeaea;
            height: 30px;
            line-height: 30px;

            .container {
                width: 1200px;
                margin: 0 auto;
                overflow: hidden;

                .loginList {
                    float: left;

                    p {
                        float: left;
                        margin-right: 10px;

                        .register {
                            border-left: 1px solid #b3aeae;
                            padding: 0 5px;
                            margin-left: 5px;
                        }
                    }
                }

                .typeList {
                    float: right;

                    a {
                        padding: 0 10px;

                        &+a {
                            border-left: 1px solid #b3aeae;
                        }
                    }

                }

            }
        }

        &>.bottom {
            width: 1200px;
            margin: 0 auto;
            overflow: hidden;

            .logoArea {
                float: left;

                .logo {
                    img {
                        width: 20%;
                        
                        margin: 20px 45px;
                    }
                }
            }

            .searchArea {
                float: right;
                margin-top: 35px;

                .searchForm {
                    overflow: hidden;

                    input {
                        box-sizing: border-box;
                        width: 490px;
                        height: 32px;
                        padding: 0px 4px;
                        border: 2px solid #41b883;
                        float: left;

                        &:focus {
                            outline: none;
                        }
                    }

                    button {
                        height: 32px;
                        width: 68px;
                        background-color: #41b883;
                        border: none;
                        color: #fff;
                        float: left;
                        cursor: pointer;
                        
                        &:focus {
                            outline: none;
                        }
                    }
                    button:hover{
                            background-color: #30a5a7;
                        }
                }
            }
        }
    }
</style>